.home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.home-title {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
}

.home-user-info {
    font-size: 16px;
    margin-bottom: 20px;
    text-align: center;
}

.home-orders-container {
    width: 80%;
    margin-bottom: 20px;
}

.home-order-item {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    margin-bottom: 20px;
}

.home-add-button {
    background-color: green;
    color: white;
    padding: 10px 20px;
    border-radius: 10px;
    cursor: pointer;
}